<template>
	<div>
		<div class="main-content-header">
				<el-breadcrumb separator-class="el-icon-arrow-right">
				  <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
				  <el-breadcrumb-item>收费项目列表</el-breadcrumb-item>
				</el-breadcrumb>
		</div>
		<div class="main-content" v-loading="loading" element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)"
			>
			<div class="opBlock">
				<div class="clearfix">
					<div class="pull-left">
						<el-button type="info" @click="refreshClick"><i class="el-icon-refresh"></i>刷新</el-button>
						<el-button type="success" @click="addClick" v-if="$auth.checkAuth($route.path,'aadd')=='1'"><i class="el-icon-plus"></i>新增</el-button>
						<el-button type="danger" @click="deleteSelectClick" v-if="$auth.checkAuth($route.path,'adelete')=='1'"><i class="el-icon-delete"></i>删除</el-button>
					</div>
					<div class="pull-right">
						<el-input v-model="searchParams.s_cnName" placeholder="学生姓名"></el-input>
						<el-button type="primary" @click="searchClick" v-if="$auth.checkAuth($route.path,'aqueryall')=='1'"><i class="el-icon-search"></i>搜索</el-button>
					</div>
				</div>
			</div>
		  <el-table
		  	
		    :data="tableData"
		    border
		    stripe
		    ref="multipleTable"
		    @selection-change="selectChange"
		    style="width: 100%">
		    <el-table-column
		      type="selection"
		      
		      min-width="55">
		    </el-table-column>
		    <el-table-column
		      prop="studentNo"
		      label="学号"
		      min-width="180">
		    </el-table-column>
		    <el-table-column
		      prop="cnName"
		      label="姓名"
		      min-width="180">
		    </el-table-column>
		    <el-table-column
		      
		      label="性别"
		      min-width="180"
		      >
		      <template slot-scope="scope"><span style="margin-left: 10px">{{ scope.row.sex=='1'?'男':'女' }}</span></template>
		    </el-table-column>
		    <el-table-column
		      prop="createTime"
		      label="创建时间"
		      min-width="180"
		      >
		    </el-table-column>
		   
		    <el-table-column
      fixed="right"
      label="操作"
      min-width="160">
      <template slot-scope="scope">
        <el-button @click="readClick(scope.row)" type="text" size="small" class="search-input">查看</el-button>
        <el-button @click="editClick(scope.row)" type="text" size="small" v-if="$auth.checkAuth($route.path,'aedit')=='1'">编辑</el-button>
        <el-button @click="deleteClick(scope.row)" type="text" size="small" v-if="$auth.checkAuth($route.path,'adelete')=='1'">删除</el-button>
      </template>
    </el-table-column>
		  </el-table>
		  <div class="pull-right mt-15">
		    <el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page.sync="pager.pageIndex"
		      :page-size="pager.pageSize"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="pager.total">
		    </el-pagination>
		  </div>
	  </div>
  </div>
</template>

<script>
import listModel from '../../assets/js/guyu/listModel'
import listData from '../../assets/data/list.json'
  export default {
  	mixins:[listModel],
    data() {
        return {
        	
        }
    },

    methods: {
    	
	},
	mounted() {//页面刚进来时获取数据
		//console.log(JSON.stringify(this.$route.path)+"==========");
		this.init({
			"tableName":"edu_student",
			"searchParams":{
				s_cnName:""
			},
			"pager":{},
			"h_detailurl":"/detail",
			"getCallBack":function(rsp){
				console.log('getCallBack');
			}
		});
		var rsp=listData;
		this.tableData=rsp.data;
		this.pager=rsp.page;
		//this.getData();
	}
  }
</script>
<style scoped>
	.el-input {
		width: 160px;
	}
</style>



