import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/common/index'
import index1 from '@/components/home'
import list from '@/components/template/list'
import detail from '@/components/template/detail'
import login from '@/components/login/login'
import changePassword from '@/components/base/ChangePassword'
import GetLocalJSON from '@/components/template/GetLocalJSON'
import chart from '@/components/chart/chart'
import calandar from '@/components/calandar/calandar'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',

      component: index,
      children:[
      	{
      		path: '',
          component: index1
      	},
      	{
      		path: 'list',
          component: list
      	},
      	{
      		path: 'detail',
          component: detail
      	},
      	{
      		path:'changePassword',
          component: changePassword
      		
      	},
      	{
      		path:'getLocalJSON',
          component: GetLocalJSON
      	},
      	{
      		path:'chart',
          component: chart
      	},
      	{
      		
      		path:'calandar',
          component: calandar
      	}
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: login
    }
  ]
})



