<template>
	<div class="menuWrap">
	    <el-menu 
	    	router
	        default-active="2"
	        class="el-menu-vertical-demo left-menu"
	        >
	        <el-menu-item index="/" style="border-bottom: 1px solid #e3e3e3;">
	            <i class="el-icon-star-on"></i>
	            <span slot="title">首页</span>
	        </el-menu-item>
	        <el-submenu index="8" >
	            <template slot="title">
	          	  	<i class="el-icon-setting"></i>
	          		<span>列表详情示例</span>
	          	</template>
	          	<el-menu-item-group>
	          		<el-menu-item index="/list">列表页</el-menu-item>
	          		<el-menu-item index="/detail">详情页</el-menu-item>
	        	</el-menu-item-group>
	      	</el-submenu>
	      	<el-submenu index="13" >
	            <template slot="title">
	          	  	<i class="el-icon-setting"></i>
	          		<span>图表示例</span>
	          	</template>
	          	<el-menu-item-group>
	          		<el-menu-item index="/chart">基本图例</el-menu-item>
	        	</el-menu-item-group>
	      	</el-submenu>
	      	<el-submenu index="26" >
	            <template slot="title">
	          	  	<i class="el-icon-setting"></i>
	          		<span>日历</span>
	          	</template>
	          	<el-menu-item-group>
	          		<el-menu-item index="/list">日历</el-menu-item>
	        	</el-menu-item-group>
	      	</el-submenu>
	        <el-submenu :index="item.id" :key="item.id" v-for="item of menuList">
	        	<template slot="title">
	          	  	<i class="el-icon-setting"></i>
	          		<span>{{item.funcName}}</span>
	          	</template>
	          	<el-menu-item-group>
	          		<el-menu-item :index="subItem.funcCode" :key="subItem.id"  v-for="subItem of item.childList">{{subItem.funcName}}</el-menu-item>
	          		<!--<el-submenu :index="subItem.funcCode"   v-for="ssubItem of subItem.childList">
				        <template slot="title">选项4</template>
				        <el-menu-item index="1-4-1">选项1</el-menu-item>
			        </el-submenu>-->
	        	</el-menu-item-group>
	        </el-submenu>
	        
	    </el-menu>
    </div>
</template>
<style scoped>
	.menuWrap {
		position: fixed;
		left: 0;
		top: 61px;
		z-index: 2;
		width: 200px;
		overflow: hidden;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
		max-height: 100%;
	}
	.left-menu {
		position: relative;
		width: 230px;
		max-height: 90%;
		overflow-y: scroll;
		overflow-x: hidden;
		background-color: #fff;
	    border-style: solid;
	    border-color: #cccccc;
	    border-width: 0 1px 0 0;
	    float: left;
	    padding-left: 0;
	    padding-right: 0;
	    
	    
	}
</style>
<script>
import axios from 'axios'
import jquery from 'jquery'
export default {
	data(){
		return {
			menuList:[],
			ifMenuShow:true
		}
	},
    methods: {
        showOrHideMenu(){
        	jquery(".menuWrap").toggleClass("hidden");
        	if(jquery(".menuWrap").hasClass('hidden')){
        		jquery(".main-contain").css('paddingLeft','0');
        		
        	}else{
        		jquery(".main-contain").css('paddingLeft','200px');
        	}
        	
        }
    },
    mounted() {//页面刚进来时获取数据
    	
		//this.menuList=JSON.parse(localStorage[this.$guyu.sysName+"_funclist"]);
  		/*this.menuList=[];*/
    	jquery(".left-menu").css("height",jquery(document).outerHeight()-100+'px');	
    	$(window).resize(function() {
	    	jquery(".left-menu").css("height",(jquery(document).outerHeight()-100)+'px');	
		});
    }
}
</script>



