import config from './config'
import axios from 'axios'
var Guyu=function(){

	var _apiUrl = config.localApi;
	switch (config.deployMode) {
		case "distributed":
			_apiUrl = config.localApi;
			break;
		case "nodistributed":
			_apiUrl = config.remoteApi;
			break;
		default:
			_apiUrl = config.localApi;
			break;
	}

	var self = this;
	self.apiUrl = _apiUrl;
	self.webUrl = config.webUrl;
	self.sysName = config.sysName;;
	self.unitSign = config.unitSign;
	self.deployMode = config.deployMode;
	self.runMode = config.runMode;
	self.imgUrl=config.imgUrl;
	
	//输出日志信息
	self.log = function(msg) {
		if (window["console"]) {
			if (self.runMode == "test") {
				console.log(msg);
			}
		}
	}

	/**
	/**
	* 生成视图模型
	* @param {Object} formID 表单ID
	* @param {Object} data   初始化数据
	*/
	
	self.buildViewModel = function buildViewModel(formID) {
		var $filed = $("#" + formID + " [data-filed]");
		var obj={};
		var funbody = new Array();
		$.each($filed, function(k, v) {
			var filedname = $(v).attr("data-filed");
			obj[filedname]="";
		})
		return obj;
	}
    /**
	 * 生成一个GUID
	 */
	self.buildGuid = function() {
		var guid = new Array();
		for (var i = 1; i <= 32; i++) {
			var n = Math.floor(Math.random() * 16.0).toString(16);
			guid.push(n);
			/*if((i==8)||(i==12)||(i==16)||(i==20)){
			   guid.push("-");
			}*/
		}
		return guid.join("");
	}
	/**
	 * 生成模型ID
	 */
	self.buildModelId = function() {
		//HH:24小时制  hh:小时制
		var dateStr = moment().format("YYYYMMDDHHmmss");
		var id = dateStr + guyu.buildGuid();
		return id;
	}
	
	
	self.ajaxBeforeSend = function(xhr) {
		xhr.setRequestHeader("userkey",userkey);
		xhr.setRequestHeader("sign",sign);
		xhr.setRequestHeader("unitsign",guyu.unitSign);
		var curUser = self.getCurrentUser();
		var userkey = curUser == null ? "null" : curUser["userkey"];
		var sign = curUser == null ? "null" : curUser["sign"];
		
		

		xhr.setRequestHeader("userkey", "guyu");
		xhr.setRequestHeader("sign", "");
		xhr.setRequestHeader("unitsign",self.unitSign);
	}

	self.ajaxError = function(xhr) {
		var msg = "";
		var isOk = true;
		switch (xhr.status) {
			case 413:
				msg = "该单位没有授权，请联系开发商";
				isOk = false;
				break;
			case 403:
				msg = "调用失效，请重新登录";
				isOk = false;
				self.logoff();
				return;
			case 200:
				isOk = true;
				break;
			case 0:
				isOk = true;
				break;
			default:
				isOk = false;
				msg = "服务器请求异常,错误码"+xhr.status;
				break;
		}

		if (!isOk) {
			alert(msg);
		}
	}

	self.ajaxRequest = function ajaxRequest(method, url, params, callback, beforeSend) {
		//debugger
		axios({
		  	method: method,
		  	url: self.apiUrl + url,
		  	data: params
		}).then(function(response) {
			callback(response.data);
		});
		/*self.log("request:" + JSON.stringify(params));

		if (beforeSend == undefined) {
			$.ajax({
				url: self.apiUrl + url,
				data: JSON.stringify(params),
				type: method,
				contentType: "application/json; charset=utf-8",
				dataType: "json",
				beforeSend: self.ajaxBeforeSend,
				success: callback,
				error: self.ajaxError
			});
		} else {
			$.ajax({
				url: self.apiUrl + url,
				data: JSON.stringify(params),
				type: method,
				contentType: "application/json; charset=utf-8",
				dataType: "json",
				beforeSend: beforeSend,
				success: callback,
				error: self.ajaxError
			});
		}*/

	}

	self.getUrlParams = function getUrlParams(params) {
		var url = params || location.search; //获取url中"?"符后的字串 
		var theRequest = new Object();
		if (url.indexOf("?") != -1) {
			var str = url.substr(url.indexOf("?") + 1);
			strs = str.split("&");
			for (var i = 0; i < strs.length; i++) {
				theRequest[strs[i].split("=")[0]] = decodeURI(strs[i].split("=")[1]);
			}
		}
		return theRequest;
	}

	

	self.getUserID = function getUserID() {
		return "1";
		//return self.getCurrentUser().userid;
		
	}

	self.getUserName = function getUserName() {
		var userName = self.getCurrentUser().nickname;
		return userName;
	}

	self.getCurrentUser = function getCurrentUser() {
		var user={};
       	try{
       	  	//var $userInfo=$.cookie("userinfo");
       	  	var $userInfo=$.cookie(self.sysName+"_userinfo");
       	  	
       	  	if($userInfo==undefined||$userInfo==null||$userInfo=="null"){
       	  		
       	  	    self.logoff();
       	  	  	return;
       	  	}
		   	user=JSON.parse($userInfo);
		}
		catch(e){
		}
       	return user;
       	  
	}
	
	self.getUserType = function getUserType() {
			return localStorage[self.sysName + "_usertype"];
	}
		/**
		 * 设置用户登录信息
		 * @param {Object} logininfo
		 */
	self.setCurrentUser = function setCurrentUser(logininfo, isRemember) {

		
        var userinfo=JSON.stringify(logininfo.userinfo);
       	if(isRemember){
       	  	$.cookie(self.sysName+"_userinfo",userinfo,{expires:7,path:'/'});
       	}
       	else{
       	  	$.cookie(self.sysName+"_userinfo",userinfo,{path:'/'});
       	}
       	  
       	if(typeof(Storage)!=="undefined"){
       		localStorage[self.sysName+"_funclist"]=JSON.stringify(logininfo.funclist);
       	}
       	
       	var funcauth={};
       	$.each(logininfo.funclist,function(k,v){
       	  	$.each(v.childList,function(sk,sv){
       	  	 	funcauth[sv.funcCode]=sv.auth;
       	  	})
       	})
       	localStorage[self.sysName+"_funcauth"]=JSON.stringify(funcauth);
	}


	//退出登录
	self.logoff = function() {
		$.cookie(self.sysName + '_userinfo', null, {
			path: '/'
		});
		var loginurl = "/#/login";
		var exitUrl="http://"+window.location.host+loginurl;
		window.top.location.href=exitUrl;
	}
	
	// 修改密码
	self.alterPassword = function() {
		
	}	


}

var guyu=new Guyu();
export default guyu;











