<template>
	<div>
	  <!-- 头部  -->
		<GuyuAdminHeader v-on:menuClick="menuClick"></GuyuAdminHeader>
		<main class="relative main">
				<!-- 菜单  -->
				<GuyuAdminNavbar ref="navbar"></GuyuAdminNavbar>
				<!-- 具体内容  -->
	  			<router-view class="main-contain"/>
		</main>
	</div>
</template>

<script>
import GuyuAdminNavbar from '@/components/common/GuyuAdminNavbar'
import GuyuAdminHeader from '@/components/common/GuyuAdminHeader'
export default {
  	name: 'app',
  	components: {
	    GuyuAdminNavbar,GuyuAdminHeader
	  },
	  methods:{
	  	menuClick(){
	  		this.$refs.navbar.showOrHideMenu(); 
	  	}
	  }
}
</script>

<style>

.main {
	padding: 0;
	padding-top: 61px;
}
.main-contain {
	position: relative;
	padding-left: 200px;
		
}
</style>



