<template>
	<div>
		<div class="main-content-header">
				<el-breadcrumb separator-class="el-icon-arrow-right">
				  <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
				  <el-breadcrumb-item>基础图表</el-breadcrumb-item>
				</el-breadcrumb>
		</div>
		<div class="main-content">
			<div class="zztChart ui-chart" id="zztChart"></div>
			<div class=" ui-chart" id="lineChart"></div>
			<div class=" ui-chart" id="bingChart"></div>
		</div>
    </div>
</template>

<script>
	// 引入 ECharts 主模块
	var echarts = require('echarts/lib/echarts');
	// 引入柱状图
	require("echarts/lib/chart/line");

	require("echarts/lib/chart/bar");
	
	require("echarts/lib/chart/pie");
	// 引入提示框和标题组件
	require('echarts/lib/component/tooltip');
	require('echarts/lib/component/title');
	require('echarts/lib/component/legend');
  export default {
    data() {
        return {
        	
        }
    },

    methods: {
    	
	},
	mounted() {
		var myChart = echarts.init(document.getElementById('zztChart'));
		myChart.setOption({
		    title: {
		        text: '世界人口总量',
		        subtext: '数据来自网络'
		    },
		    tooltip: {
		        trigger: 'axis',
		        axisPointer: {
		            type: 'shadow'
		        }
		    },
		    legend: {
		        data: ['2011年', '2012年']
		    },
		    grid: {
		        left: '3%',
		        right: '4%',
		        bottom: '3%',
		        containLabel: true
		    },
		    xAxis: {
		    	type: 'category',
		        data: ['巴西','印尼','美国','印度','中国','世界人口(万)']
		       
		    },
		    yAxis: {
		         type: 'value',
		        boundaryGap: [0, 0.01]
		    },
		    series: [
		        {
		            name: '2011年',
		            type: 'bar',
		            data: [18203, 23489, 29034, 104970, 131744, 630230]
		        },
		        {
		            name: '2012年',
		            type: 'bar',
		            data: [19325, 23438, 31000, 121594, 134141, 681807]
		        }
		    ]
		});
		var lineChart = echarts.init(document.getElementById('lineChart'));
		lineChart.setOption({
		    title: {
		        text: '对数轴示例',
		        left: 'center'
		    },
		    tooltip: {
		        trigger: 'item',
		        formatter: '{a} <br/>{b} : {c}'
		    },
		    legend: {
		        left: 'left',
		        data: ['2的指数', '3的指数']
		    },
		    xAxis: {
		        type: 'category',
		        name: 'x',
		        splitLine: {show: false},
		        data: ['一', '二', '三', '四', '五', '六', '七', '八', '九']
		    },
		    grid: {
		        left: '3%',
		        right: '4%',
		        bottom: '3%',
		        containLabel: true
		    },
		    yAxis: {
		        type: 'log',
		        name: 'y'
		    },
		    series: [
		        {
		            name: '3的指数',
		            type: 'line',
		            data: [1, 3, 9, 27, 81, 247, 741, 2223, 6669]
		        },
		        {
		            name: '2的指数',
		            type: 'line',
		            data: [1, 2, 4, 8, 16, 32, 64, 128, 256]
		        },
		        {
		            name: '1/2的指数',
		            type: 'line',
		            data: [1/2, 1/4, 1/8, 1/16, 1/32, 1/64, 1/128, 1/256, 1/512]
		        }
		    ]
		});
		var bingChart = echarts.init(document.getElementById('bingChart'));
		bingChart.setOption({
		    title : {
		        text: '某站点用户访问来源',
		        subtext: '纯属虚构',
		        x:'center'
		    },
		    tooltip : {
		        trigger: 'item',
		        formatter: "{a} <br/>{b} : {c} ({d}%)"
		    },
		    legend: {
		        orient: 'vertical',
		        left: 'left',
		        data: ['直接访问','邮件营销','联盟广告','视频广告','搜索引擎'],
		        selected:{
		        	"直接访问":true
		        }
		    },
		    series : [
		        {
		            name: '访问来源',
		            type: 'pie',
		            radius : '55%',
		            center: ['50%', '60%'],
		            data:[
		                {value:335, name:'直接访问'},
		                {value:310, name:'邮件营销'},
		                {value:234, name:'联盟广告'},
		                {value:135, name:'视频广告'},
		                {value:1548, name:'搜索引擎'}
		            ],
		            itemStyle: {
		                emphasis: {
		                    shadowBlur: 10,
		                    shadowOffsetX: 0,
		                    shadowColor: 'rgba(0, 0, 0, 0.5)'
		                }
		            }
		        }
		    ]
		});
		
	}
  }
</script>
<style scoped>
	
	.ui-chart {
		width: 100%;
		height: 350px;
		padding: 15px;
	}
	.zztChart {
		
	}
	
</style>



