// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Element from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import './assets/css/base.css'
import './assets/css/common.css'
import './assets/css/img.css'
import guyu from './assets/js/guyu/core'
import auth from './assets/js/guyu/auth'
import custom from './assets/js/guyu/custom'
import  './assets/js/plug/jquery.md5'
import  './assets/js/plug/jquery.cookie'
Vue.use(Element, { size: 'small' })

Vue.config.productionTip = false
guyu.custom=custom;
Vue.prototype.$guyu=guyu;
Vue.prototype.$auth=auth;
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



