<template>
	<div class="main">
		<div class="blueBg"></div>
	  	<el-form id="form" ref="form" :rules="rules" :model="loginModel" label-width="0">
	  		<h4 class="login-title">后台管理系统</h4>
	  		<div class="logoBlock home-ico-bg"></div>
	  		<div class="logoBlockWrap"></div>
	  		<el-row>
			  	<el-col :span="24">
			  		<el-form-item  prop="userName">
					    <el-input data-field="userName" placeholder="用户名" v-model="loginModel.userName">
					    	<template slot="prepend">用户名</template>
					    </el-input>
				    </el-form-item>
			  	</el-col>
			</el-row>
			<el-row>
			  	<el-col :span="24">
			  		<el-form-item prop="passwordOrigin">
				  		<el-input type="password" data-field="passwordOrigin" placeholder="密码" v-model="loginModel.passwordOrigin">
					    	<template slot="prepend">密&nbsp;&nbsp;&nbsp;码</template>
					    </el-input>
				    </el-form-item>
			  	</el-col>
			</el-row>
			<el-row>
			  	<el-col :span="24" class="checkCodeWrap">
			  		<el-form-item prop="checkCode">
				  		<el-input class="checkCode" data-field="checkCode" placeholder="验证码" v-model="loginModel.checkCode">
					    	<template slot="prepend">验证码</template>
					    </el-input>
					    <img class="checkCodeImg" v-bind:src="loginModel.checkCodeImgUrl" @click="getCheckCode" />
				    </el-form-item>
			  	</el-col>
			</el-row>
			<el-row>
			  	<el-col :span="24">
			  		<button class="loginBtn" type="button" @click="loginClick">登录</button>
			  	</el-col>
			</el-row>
	  	</el-form>
	</div>
</template>

<script>
import axios from 'axios'
import jquery from 'jquery'
export default {
  	name: 'login',
	methods:{
	  	loginClick(){
	  		
	  		this.$refs["form"].validate((valid) => {
	  			if (valid) {
	  				this.$message.error("登录成功");
	  				this.$router.push('/');
	  				
	  			}else{
	  				return false;
	  			}
	  			
	  		});
	  	},
	  	getCheckCode(){//切换验证码
	  		this.loginModel.checkCodeImgUrl=require('../../assets/img/checkcode.jpg');  
	  	}
	},
	data(){
		return {
			loading:false,
			loginModel:{
				tableName:"",
				userName:"",
				password:"",
				passwordOrigin:"",
				checkCode:"",
				checkCodeImgUrl:require('../../assets/img/checkcode.jpg'),
				isRemember:true
			},
			rules: {
	        	userName : [
	        		{ required: true, message: '请输入用户名', trigger: 'blur' }
	        	],
	        	passwordOrigin : [
	        		{ required: true, message: '请输入密码', trigger: 'blur' },
            		{ min:6,max: 20, message: '密码长度大于6小于20', trigger: 'blur' }
	        	],
	        	checkCode : [
	        		{ required: true, message: '请输入验证码', trigger: 'blur' }
	        	]
	       },
	       options: {//
	       	    loginUrl:"/api/base/login",
	       	    tableName:"sys_user",
	       	    h_returnurl:"/html/home/index.html",
	       	    a_codeurl:"/api/base/checkcode",
	       }
		}
	},
	mounted() {
		
	}
}
</script>

<style scoped>
	.main {
		background-color: #fff;
	}
	#form {
	    position: absolute;
	    top: 0;
	    bottom: 0;
	    left: 0;
	    right: 0;
	    z-index: 3;
	    margin: auto;
	    width: 400px;
	    height: 350px;
	    padding: 50px;
	    padding-top: 75px;
	    box-shadow: 10px 10px 5px #999;
	    text-align: left;
	    background-color: #fff;
	    border: 1px solid #e3e3e3;
	}
	.blueBg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 50%;
		z-index: 2;
		background-color: #00acc1;
	}
	.el-input {
		margin-bottom: 5px;
	}
	.loginBtn {
		width: 100%;
		height: 35px;
		margin-top: 20px;
		color: #fff;
		border-radius: 5px;
		border: none;
		background-color: #00acc1;
	}
	.logoBlock {
		position: absolute;
		top: -80px;
		left: 0;
		right: 0;
		z-index: 3;
		margin: auto;
		width: 130px;
		height: 130px;
		background-color: #fff;
		border: 1px solid #e3e3e3;
		border-radius: 50%;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
	}
	.login-title {
		position: absolute;
	    top: 85px;
	    left: -373px;
	    color: #fff;
	    font-size: 26px;
	    letter-spacing: 0.3em;
	}
	.checkCode input {
		width: 100px;
	}
	.checkCodeWrap {
		position: relative;
	}
	.checkCodeImg {
		position: absolute;
		right: 0;
		top: 0;
		display: inline-block;
		width: 100px;
		height: 32px;
		border: 1px solid #e3e3e3;
		border-radius: 5px;
	}
	.logoBlockWrap {
		position: absolute;
		top: -85px;
		left: 0;
		right: 0;
		z-index: 2;
		margin: auto;
		width: 140px;
		height: 140px;
		background-color: #fff;
		border: none;
		border-radius: 50%;
		background-color: #00acc1;
	}
</style>



