<template>
	<div>
		<div class="main-content-header">
				<el-breadcrumb separator-class="el-icon-arrow-right">
				    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
				    <el-breadcrumb-item>收费项目详情</el-breadcrumb-item>
				</el-breadcrumb>
		</div>
		<div class="main-content"  v-loading="loading"   element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)">
			<div class="opBlock">
				<div class="clearfix">
					<div class="pull-left">
						<el-button type="info" @click="gobackClick"><i class="el-icon-arrow-left"></i>返回</el-button>
						<el-button type="success" @click="submitClick" v-if="options.operation!='read'" ><i class="el-icon-check"></i>保存</el-button>
						<el-button type="success" @click="submitAndAddClick" v-if="options.operation!='read'" ><i class="el-icon-plus"></i>保存并新增</el-button>
					</div>
				</div>
			</div>
			<el-form id="form" ref="form" :rules="rules" :model="model" label-width="105px">
				
				<el-input data-field="motto" v-model="model.motto" class="hidden"></el-input>
				<el-input data-field="id" v-model="model.id" class="hidden"></el-input>
				<el-row>
				  	<el-col :span="12">
				  		<el-form-item label="学号:" prop="studentNo" required>
						    <el-input data-field="studentNo" v-model="model.studentNo"></el-input>
						</el-form-item>
				  	</el-col>
				  	<el-col :span="12">
				  		<el-form-item label="姓名:" prop="cnName" required>
						    <el-input  data-field="cnName" v-model="model.cnName"></el-input>
						</el-form-item>
				  	</el-col>
				</el-row>
				<el-row>
					<el-col :span="12">
					  	<el-form-item label="性别:" data-field="sex" prop="sex" required>
						    <el-radio-group v-model="model.sex">
						      <el-radio label="1">男</el-radio>
						      <el-radio label="0">女</el-radio>
						    </el-radio-group>
						</el-form-item>
					 </el-col>
				  	<el-col :span="12">
				  		<el-form-item label="出生年月:" data-field="birthday">
					  		<el-date-picker
						      v-model="model.birthday"
						       
						      type="date"
						      placeholder="选择日期"
						      >
						    </el-date-picker>
					    </el-form-item>
				  	</el-col>
				</el-row>
				<el-row>
					<el-col :span="12">
					  	<el-form-item label="入学月份:" data-field="sex">
						    <el-select v-model="model.schoolMonth" placeholder="入学月份">
						      
						      <el-option label="一月" value="01"></el-option>
							<el-option label="二月" value="02"></el-option>
							<el-option label="三月" value="03"></el-option>
							<el-option label="四月" value="04"></el-option>
							<el-option label="五月" value="05"></el-option>
							<el-option label="六月" value="06"></el-option>
							<el-option label="七月" value="07"></el-option>
							<el-option label="八月" value="08"></el-option>
							<el-option label="九月" value="09"></el-option>
							<el-option label="十月" value="10"></el-option>
							<el-option label="十一月" value="11"></el-option>
							<el-option label="十二月" value="12"></el-option>
						    </el-select>
						 </el-form-item>
					 </el-col>
					<el-col :span="12">
					  	<el-form-item label="入学方式:" data-field="entrance">
						    <el-select v-model="model.entrance" placeholder="入学方式">
						    	
						    	<el-option v-bind:label="item.codeName" v-bind:value="item.codeNo" :key="item.id"  v-for="item of entranceOptions"></el-option>
						    	 
						    </el-select>
						 </el-form-item>
					 </el-col>
				</el-row>
				<!-- 富文本编辑器 -->
				<el-row>
					<el-col :span="24">
						<el-form-item label="座右铭:" data-field="motto">
							<quill-editor ref="myTextEditor"
							    :content="model.motto"
							    :options = "editorOption"
							    @change="onEditorChange($event)">
							</quill-editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
	  </div>
  </div>
</template>
<style scoped>
	.el-form {
		padding: 15px;
		background-color: #fff;
		border-top: 1px solid #e3e3e3;
	}
	.el-col {
		padding-left: 15px;
		padding-right: 15px;
	}
	.el-select {
		width: 100%;
	}
</style>
<script>
import detailModel from '../../assets/js/guyu/detailModel'
import customMixn from '../../assets/js/guyu/customMixn'
import { quillEditor } from 'vue-quill-editor'
import detailData from '../../assets/data/detail.json'
  export default {
  	components: {
    	quillEditor
   },
  	mixins:[detailModel,customMixn],
    data() {
        return {
	        rules: {
	        	studentNo : [
	        		{ required: true, message: '这是必填字段', trigger: 'blur' },
            		{ max: 20, message: '长度小于20个字符', trigger: 'blur' }
	        	],
	        	cnName: [
	        		{ required: true, message: '这是必填字段', trigger: 'blur' }
	        	],
	        	sex : [
	        		{ required: true, message: '这是必填字段', trigger: 'blur' }
	        	]
	        },
	        entranceOptions:[
		        {
		        	codeNo:"",
		        	codeName:"请选择"
		        }
		    ],
		    content: '',
		    editorOption: {
		       placeholder: '请输入内容'
		    }
        }
    },methods: {
	    onEditorChange({ editor, html, text }) {//富文本编辑器  文本改变时 设置字段值
	        this.model.motto = html
	    }
	},mounted() {//页面刚进来时获取数据
		this.init({
			"formId":"form",//表单id 
			"tableName":"edu_student",//表名
			"operation":this.$route.query.op,//操作 add read edit
			"apiParams":{//请求参数
				id:this.$route.query.id
			},
			"getCallBack":function(rsp){//获取数据回调方法
				console.log('getCallBack');
			},
			"submitAndAddCallBack":function(rsp){//获取数据回调方法
				console.log('submitAndAddCallBack');
			},
			"submitCallBack":function(rsp){//获取数据回调方法
				console.log('submitCallBack');
			}
		});
		if(this.options.operation!='add'){
			var rsp=detailData;
			this.model=rsp.data;
			//this.getData();
		}
		this.getSelectOptions("ENTRANCE_TYPE","entrance",function(rsp){
			
		});
	}
  }
</script>




