var custom = {
	getSysCode:function (parentNo, viewmodel, filedName,callback) {
		var data = {};
		data.tableName = "sys_code";
		data.userid = guyu.getUserID();
		data.apiParams = {};
		data.apiParams.listType = "all";
		data.apiParams.parentNo = parentNo;
		guyu.ajaxRequest("post", "/api/base/list", data, function(rsp) {
			if(rsp.status > 0) {
				if(callback!=undefined){
					callback(rsp);
				}
			}
		});
	}
}

export default custom;



