/**
 * 列表模型
 */

var listModel= {
	created:function(){
		
	},
	methods:{
		refreshClick:function(){//刷新
			window.location.reload();
		},
		addClick:function(){//添加
			this.$router.push({path:this.options.h_detailurl,query: { op: 'add' }});
		},
		readClick:function(row){//只读
			this.$router.push({path:this.options.h_detailurl,query: { op: 'read',id:row.id }});
		},
		deleteClick:function(row){//删除
			this.$confirm('此操作将永久删除, 是否继续?', '提示', {
	            confirmButtonText: '确定',
	            cancelButtonText: '取消',
	            type: 'warning'
	        }).then(() => {
            	this.$message.error("该行已删除");
	   
	        }).catch(() => {
	                     
	        });
			
		},
		deleteSelectClick:function(){//删除选中
			this.$confirm('此操作将永久删除, 是否继续?', '提示', {
	            confirmButtonText: '确定',
	            cancelButtonText: '取消',
	            type: 'warning'
	        }).then(() => {
            	this.$message.error("删除选中行");
	      
	        }).catch(() => {
	                     
	        });
			
		},
		editClick:function(row){//编辑
			this.$router.push({path:this.options.h_detailurl,query: { op: 'edit',id:row.id }});
		},
		searchClick:function(){//搜索
			this.pager.pageIndex=1;
			this.$message.error("搜索学生姓名："+this.searchParams.s_cnName);
		},
		handleSizeChange:function(val){
			
			this.pager.pageSize=val ;
			this.$message.error("每页"+val+"条数据,获取数据");
			//this.getData();
		},
		handleCurrentChange:function(val){//分页索引改变时
			this.$message.error("跳转到第"+val+"页,获取数据");
			this.pager.pageIndex=val ;
			//this.getData();
		},
		getData:function(){//获取数据
			this.loading=true;
			this.searchParams.pageIndex=this.pager.pageIndex;
			this.searchParams.pageSize=this.pager.pageSize;
			this.$guyu.ajaxRequest("post", this.options.a_geturl, {"tableName":this.options.tableName,"userid":this.userid,"apiParams":this.searchParams}, (rsp)=>{
	  			if(rsp.status>0){
	  				if(this.options.getCallBack!=undefined){
						this.options.getCallBack(rsp);
					}
		  			this.tableData=rsp.data;
		  			this.pager=rsp.page;
	  			}else{
	  				this.$message.error(rsp.msg);
	  			}
	  			
	  			
	  			this.loading=false;
	  		});
		},
		deleteRows:function(rows){//请求删除数据
			this.$message.error("删除行成功");
			
		},
		selectChange:function(val){
			this.multipleSelection=val;
			//this.deleteParams.idList.push(val.id);
		},
		init:function(opts){//初始化数据
			for(var i in opts) {
				this.options[i] = opts[i];
			}
			for(var a in this.options.searchParams) {
				this.searchParams[a]=this.options.searchParams[a];
			}
			for(var b in this.options.pager) {
				this.pager[b]=this.options.pager[b];
			}
		}
	},
	data(){
		return {
			options:{//选项
				a_geturl: "/api/base/list",
				a_deleteurl: "/api/base/delete",
				a_importurl: "/api/base/import",
				a_exporturl: "/api/base/export"
			},
			userid:this.$guyu.getUserID(),//用户id
			searchParams:{//搜索参数
				listType:"part"
			},
			deleteParams:{//删除行 参数
				"idList":[],
				"deleteUserId":"1",
				"deleteUserName":"1",
			},
			pager:{//分页
				"pageSize": 10,
			    "pageIndex": 1,
			    "total": 0
			},
			multipleSelection:[],//选中的复选框
			loading:false,//是否显示加载中界面
			tableData: []//表格数据
		}
		
	}
}

export default listModel;


