/**
 * 列表模型
 */

var detailModel= {
	created:function(){
		
	},
	methods:{
		gobackClick:function(){//返回
			this.$router.go(-1);
		},
		submitClick:function(){//保存
			//表单验证
			this.$refs[this.options.formId].validate((valid) => {
	          	if (valid) {
	          		var submitUrl="";
	          		if(this.options.operation=='add'){
	          			submitUrl=this.options.a_addurl;
	          		}else{
	          			submitUrl=this.options.a_updateurl;
	          		}
	            	this.$message("保存成功");
	          	} else {
	            	
	            	return false;
	          	}
	        });
		},
		submitAndAddClick:function(){//保存并新增
			//表单验证
			this.$refs[this.options.formId].validate((valid) => {
	          	if (valid) {
	            	var submitUrl="";
	          		if(this.options.operation=='add'){
	          			submitUrl=this.options.a_addurl;
	          		}else{
	          			submitUrl=this.options.a_updateurl;
	          		}
	          		this.$message("保存并新增成功");
	            	
	          	} else {
	            	
	            	return false;
	          	}
	        });
		},
		getData:function(){//获取数据
			this.loading=true;
			this.$guyu.ajaxRequest("post", this.options.a_geturl, {"tableName":this.options.tableName,"userid":this.userid,"apiParams":this.apiParams}, (rsp)=>{
				if(rsp.status>0){
	  				/*this.$message({
	  					message:rsp.msg,
          				type: 'success'
          			});*/
          			this.model=rsp.data;
          			this.initData=rsp.data;
					if(this.options.getCallBack!=undefined){
						this.options.getCallBack(rsp);
					}
	  			}else{
	  				this.$message.error(rsp.msg);
	  			}
				this.loading=false;
			});
		},
		init:function(opts){//初始化数据
			for(var i in opts) {
				this.options[i] = opts[i];
			}
			this.model=this.$guyu.buildViewModel(this.options.formId);
			if(this.options.operation!='add'){
				this.apiParams=this.options.apiParams;
				//this.getData();
			}
		}
	},
	data(){
		return {
			options:{
				a_geturl: "/api/base/detail",
				a_addurl: "/api/base/add",
				a_updateurl: "/api/base/update",
				formId:"form"
			},
			userid:1,
			model:{},//数据模型
			initData:null,
			apiParams:{},//请求参数
			loading:false//显示加载中loading
		}
		
	}
}

export default detailModel;


