/**
 * 列表模型
 */

var customMixn= {
	created:function(){
		
	},
	methods:{
		getSelectOptions:function(parentNo,filedName,callBackFunc){//获取syscode
			var data = {};
			data.tableName = "sys_code";
			data.userid = this.$guyu.getUserID();
			data.apiParams = {};
			data.apiParams.listType = "all";
			data.apiParams.parentNo = parentNo;
			this.$guyu.ajaxRequest("post", "/api/base/list", data, (rsp)=>{
				if(rsp.status > 0) {
					var obj = {
						"codeNo": null,
						"codeName": "默认",
						"codeOrder": "",
						"parentNo": "",
						"codeFlag": "",
						"codeSc":"",
						"id": null,
						"rowNum": null
					}
					rsp.data.splice(0, 0, obj);
					this[filedName+ "Options"]=rsp.data;
					if(this.initData != null && this.options.operation != 'add') {
						this[filedName]=this.initData[filedName];
					}
					if(callBackFunc!=null){
						callBackFunc(rsp);
					}
				}
			});
		}
	}
}

export default customMixn;


