<template>
	<div>
		<div class="main-content-header">
			<el-breadcrumb separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
			  <el-breadcrumb-item>修改密码</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="main-content">
			<el-form id="form" ref="form" :rules="rules" :model="model">
				<el-row>
				  	<el-col :span="24">
				  		<el-form-item  prop="oldPassword">
						    <el-input type="password" placeholder="旧密码" v-model="model.oldPassword">
						    	<template slot="prepend">旧密码&nbsp;&nbsp;&nbsp;</template>
						    </el-input>
					    </el-form-item>
				  	</el-col>
				</el-row>
				<el-row>
				  	<el-col :span="24">
				  		<el-form-item prop="newPassword">
					  		<el-input type="password"  placeholder="新密码" v-model="model.newPassword">
						    	<template slot="prepend">新密码&nbsp;&nbsp;&nbsp;</template>
						    </el-input>
					    </el-form-item>
				  	</el-col>
				</el-row>
				<el-row>
				  	<el-col :span="24">
				  		<el-form-item prop="reNewPassword">
					  		<el-input type="password"  placeholder="确认密码" v-model="model.reNewPassword">
						    	<template slot="prepend">确认密码</template>
						    </el-input>
					    </el-form-item>
				  	</el-col>
				</el-row>
				<el-row>
			  	<el-col :span="24">
			  		<button class="commitBtn" type="button" @click="submitClick">提交</button>
			  	</el-col>
			</el-row>
			</el-form>
		</div>
    </div>
</template>

<script>
import jquery from 'jquery'
export default {
    data() {
    	var validatePass2 = (rule, value, callback) => {
	        if (value !== this.model.newPassword) {
	            callback(new Error('两次输入密码不一致!'));
	        } else {
	            callback();
	        }
        };
        return {
        	rules:{
        		oldPassword: [
		            { required: true, message: '请输入旧密码', trigger: 'blur' }
		        ],
		        newPassword: [
		            { required: true, message: '请输入新密码', trigger: 'blur' },
		            { min:6,max: 20, message: '密码长度大于6小于20', trigger: 'blur' }
		        ],
		        reNewPassword: [
		            { required: true, message: '请输入确认密码', trigger: 'blur' },
		            { min:6,max: 20, message: '密码长度大于6小于20', trigger: 'blur' },
		            { validator: validatePass2, trigger: 'blur' }
		        ]
        	},
        	model:{
        		oldPassword:"",
        		newPassword:"",
        		reNewPassword:"",
        		id:""
        	}
        }
    },

    methods: {
    	submitClick(){
    		this.$refs["form"].validate((valid) => {
	  			if (valid) {
	  				this.model.id=this.$guyu.getUserID();
	  				this.model.oldPassword=jquery.md5(this.model.oldPassword);
	  				this.model.newPassword=jquery.md5(this.model.newPassword);
	  				this.model.reNewPassword=jquery.md5(this.model.reNewPassword);
	  				this.$message.error("密码修改成功");
	  			}
	  		})
    	}
	}
}
</script>
<style scoped>
	#form {
		margin: auto;
		margin-top: 35px;
		width: 400px;
		height: 300px;
	}
	.commitBtn {
		width: 100%;
		height: 35px;
		margin-top: 20px;
		color: #fff;
		border-radius: 5px;
		border: none;
		background-color: #00acc1;
	}
</style>



